body{
  margin: 0;
  font-family: arial, sans-serif;
  display: flex;
  justify-content: center;
  height: 100vh;
  background-color: seashell;
}
.app{
  text-align: center;
  width: 98%;
}
h1{
  margin-bottom: 30px;
  font-size: 32px;
}

table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px 0;
  }

th, td {
  
  padding: 8px 12px;
  border: 2px solid gray;
  text-align: left;
  
}
button {
  margin: 5px;
  padding: 10px;
  background-color: grey;
  color: white;
  border: none;
  border-radius: 10px;
  cursor: pointer;
}

button:hover {
  background-color: black;
}

button.selected {
  background-color: black;
  color: white;
}

.genre-filter {
  background-color: white;
  padding: 20px;
  margin: 20px auto;
  width: 100%;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}
.genre-filter h2 {
  text-align: left;  
  margin-bottom: 15px;
  font-size: 25px;
}
.genre-buttons {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.genre-filter {
  margin-bottom: 15px;
}
@media (max-width: 768px) {
  .app {
    width: 100%;
    padding: 0 10px;
  }

  .genre-filter {
    width: 90%;
    padding: 15px;
  }

  table {
    font-size: 14px;
  }

  th, td {
    padding: 6px 8px;
  }

  button {
    margin: 4px;
    padding: 8px;
    font-size: 14px;
  }

  h1 {
    font-size: 24px;
    margin-bottom: 15px;
  }

  .genre-filter h2 {
    font-size: 18px;
    margin-bottom: 10px;
  }
}

/* Smaller mobile styles */
@media (max-width: 480px) {
  .genre-filter {
    width: 95%;
    padding: 10px;
  }

  table {
    font-size: 12px;
  }

  th, td {
    padding: 4px 6px;
  }

  button {
    margin: 3px;
    padding: 6px;
    font-size: 12px;
  }

  h1 {
    font-size: 20px;
    margin-bottom: 10px;
  }

  .genre-filter h2 {
    font-size: 16px;
    margin-bottom: 8px;
  }
}